.input-wrapper {
    position: relative;
    width: 100%;
    height: 50px;
    inset: 0;
    border-radius: 16px;
    overflow: hidden;
}

.input-field {
    position: absolute;
    width: 100%;
    height: 100%;
    color: var(--color-text);
    background: var(--color-input-field);
    outline: none;
    border: none;
    padding-inline: 20px;
    font-weight: 600;
}

.input-field:hover ~ .input-background {
    border: 1px solid var(--color-input-border);
    transition: border 0.25s ease;
}

.input-field:focus ~ .input-border {
    border: 3px solid var(--color-input-border);
    transition: border 0.25s ease;
}

.input-field::placeholder {
    color: var(--color-text);
}

.input-border,
.input-background {
    position: absolute;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 2;
    border: 3px solid transparent;
    border-radius: 16px;
    transition: border 0.25s ease;
}

.input-background {
    border: 1px solid transparent;
}
